
#scrollUp {
    border-radius: 2px;
    bottom: 15px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
    color: #fff;
    font-size: 28px;
    height: 45px;
    line-height: 49px;
    position: fixed;
    right: 15px;
    text-align: center;
    transition: all 0.3s ease 0s;
    width: 45px;
    z-index: 200;
  }
  #scrollUp:hover {
  }
  
  @-webkit-keyframes scroll-ani-to-top {
    0% {
      opacity: 0;
      bottom: 0;
    }
    50% {
      opacity: 1;
      bottom: 50%;
    }
    100% {
      opacity: 0;
      bottom: 75%;
    }
  }
  @keyframes scroll-ani-to-top {
    0% {
      opacity: 0;
      bottom: 0;
    }
    50% {
      opacity: 1;
      bottom: 50%;
    }
    100% {
      opacity: 0;
      bottom: 75%;
    }
  }
  #scrollUp:hover i {
    -webkit-animation: 800ms linear 0s normal none infinite running scroll-ani-to-top;
            animation: 800ms linear 0s normal none infinite running scroll-ani-to-top;
    height: 100%;
    left: 0;
    margin-bottom: -25px;
    position: absolute;
    width: 100%;
    bottom: 0;
  }
  #scrollUp i {
    color: #fff;
  }
   #scrollUp {
    background: #c43b68 none repeat scroll 0 0;
  }
  
